@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300;400;500;600;700;800;900&display=swap);
.app{text-align:center}.app-header{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);font-weight:bold;margin:2rem}.app-content{max-width:960px;margin:0 auto}.app-footer{display:grid;place-content:center;padding:1.5rem;background-color:#011627;background-color:var(--color-1);color:#FDFFFC;color:var(--color-2)}.content-section{display:flex;margin-bottom:3rem}.content-section:nth-child(even){flex-direction:row-reverse}.content-section__item{flex:1 1 50%;padding:1rem}.content-section__img{width:100%;height:100%;object-fit:cover;border-radius:5px}.content-section__description-content{text-align:left;display:grid;align-content:center;grid-gap:.5rem}.button{border:none;padding:.5rem;background-color:#2EC4B6;background-color:var(--color-3);color:#FDFFFC;color:var(--color-2);transition:background-color 250ms linear;border-radius:5px}.button:hover{background-color:#FF9F1C;background-color:var(--color-5)}:root{--color-1: #011627;--color-2: #FDFFFC;--color-3: #2EC4B6;--color-4: #E71D36;--color-5: #FF9F1C;--font-family: "Manrope", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;--text-color: --color-1;--background-color: --color-2;--base-font-size: 16px;--base-font-weight: 400}body{margin:0;padding:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Manrope", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;font-family:var(--font-family);font-size:16px;font-size:var(--base-font-size);font-weight:400;font-weight:var(--base-font-weight);color:--color-1;color:var(--text-color);background-color:--color-2;background-color:var(--background-color)}input,button,select,textarea{font-family:"Manrope", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;font-family:var(--font-family)}a,button{cursor:pointer;color:#E71D36;color:var(--color-4);text-decoration:none}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
